import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import { platform, IOS, ANDROID } from '@vkontakte/vkui';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';

import './Home.css';

const osName = platform();

const Home = ({ id, go }) => (
	<Panel id={id}>

	{(osName === IOS) && 
		<React.Fragment className="ios">
			<PanelHeader className="iosHeader">О сервисе</PanelHeader>
				<Group className="iosGroup" title="+100 к карме">
					<Div> 
						<p className="iosInfo">Привет. Задумывался ли ты о том как повысить свою карму? Благотворительность - одно из сопуствующих в данном деле. Привет. Задумывался ли ты о том как повысить свою карму? Благотворительность - одно из сопуствующих в данном деле.</p>
						<Button className="iosBtnDone" size="xl" level="2" onClick={go} data-to="persik">
							Вперед
						</Button>
					</Div>
				</Group>
		</React.Fragment>
	}

	{(osName === ANDROID) && 
		<React.Fragment className="android">
			<PanelHeader className="">О сервисе</PanelHeader>
			<Group className="androidGrout" title="+100 к карме">
				<Div> 
					<p className="androidInfo">
					Привет. Задумывался ли ты о том как повысить свою карму? Благотворительность - одно из сопуствующих в данном деле.
					</p>
					<Button className="androidBtnDone" size="xl" level="2" onClick={go} data-to="persik">
						Готово
					</Button>
				</Div>
			</Group>
		</React.Fragment>
	}

	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Home;
